export default {
	data() {
		return {
			shopStat: 0
		}
	},
	onLoad(option){
		this.shopStat = option.id
	},
	methods: {
		statRequest(){
			uni.request({
				url: 'https://www.finetwm.com/xmRepair/shopM/updateStat',
				method: 'POST',
				header:{
					"content-Type": "application/x-www-form-urlencoded"
				},
				data: {
					stat: 1,
					openid: this.openId,
					opinion: ''
				},
				success: res => {
					console.log(res)
				},
				fail: err => {
					console.log(err)
				}
			})
		},
		shopStatRequest(){
			this.shopStat = 5
		},
		onNext(e){
			if(e == 0 || e == 1 ){
				uni.reLaunch({
					url: '../index/index'
				})
			} else if(e == 2){
				uni.navigateTo({
					url: 'business-in'
				})
			} else if( e == 4 ){
				uni.navigateBack()
			} else if( e == 5){
				uni.navigateTo({
					url: 'contract'
				})
			} else if(e == 3){
				uni.navigateTo({
					url: 'contract'
				})
			} else if(e == 6){
				uni.navigateTo({
					url: 'upload-photo'
				})
			} else if( e == 8) { 
				this.statRequest()
			} else {
				return
			}
		}
	}
}
